import React from 'react'
import "./navbarStyle.scss";

import RightArrow from "../../Images/Icons/arrow_right.png";

const NavbarElement = ({
    elementText = "",
    contextArray = [],
    urlTo = ""
}) => {
    const displayContextMenu = () => {
        const arrayMap = contextArray.length > 0 ? contextArray.map(element => {
            return (<li key={element.id}><a href={element.urlTo}>{element.elementText}</a></li>)
        }) : null;

        const drawLI = () => {
            return (
                <div className="navbar-list-item-dropdown">
                    <ul>{arrayMap}</ul>
                </div>
            )
        }

        return contextArray.length > 0 ? drawLI() : null;       
    }

    return (
        <div className="navbar-list-item">
            <a className="navbar-list-item-a" href={urlTo}>{elementText}</a>
            <img src={RightArrow} alt="right_arrow"/>
            {displayContextMenu()}
        </div>
    )
}

export default NavbarElement;